/* FeaturesList.css */
:root {
  --primary-color: #4361ee;
  --secondary-color: #3f37c9;
  --accent-color: #4cc9f0;
  --light-color: #f8f9fa;
  --dark-color: #212529;
  --gray-color: #6c757d;
  --border-radius: 8px;
  --box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  --transition: all 0.3s ease;
}

.features-app {
  min-height: 100vh;
  background: linear-gradient(to bottom, rgba(241, 238, 238, 0.7) 30%, rgba(97, 205, 153, 0.5) 70%, transparent 100%);
}

.app-header {
  background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
  color: white;
  padding: 1rem 0;
  position: sticky;
  top: 0;
  z-index: 1000;
  box-shadow: var(--box-shadow);
}

.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

.logo-link {
  text-decoration: none;
}

.logo {
  font-size: 1.8rem;
  font-weight: 700;
  color: white;
  letter-spacing: 1px;
}

.menu-toggle {
  background: none;
  border: none;
  color: white;
  font-size: 1.5rem;
  cursor: pointer;
  display: none;
  padding: 0.5rem;
}

.menu-icon {
  display: block;
  width: 25px;
  height: 2px;
  background-color: white;
  position: relative;
  transition: var(--transition);
}

.menu-icon::before,
.menu-icon::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: white;
  transition: var(--transition);
}

.menu-icon::before {
  top: -8px;
}

.menu-icon::after {
  top: 8px;
}

.menu-icon.open {
  background: transparent;
}

.menu-icon.open::before {
  transform: rotate(45deg);
  top: 0;
}

.menu-icon.open::after {
  transform: rotate(-45deg);
  top: 0;
}

.nav-links {
  display: flex;
  gap: 1.5rem;
}

.nav-links a {
  color: white;
  text-decoration: none;
  font-weight: 500;
  padding: 0.5rem 1rem;
  border-radius: var(--border-radius);
  transition: var(--transition);
}

.nav-links a:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.features-wrapper {
  text-align: center;
  padding: 4rem 2rem;
  max-width: 1400px;
  margin: 0 auto;
}

.features-header {
  margin-bottom: 3rem;
}

.features-title {
  font-size: 1.8rem;
  margin-bottom: 0.5rem;
  background: linear-gradient(90deg, #ff6b61, #61cd99, #61a0ff, #ffb347);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  font-weight: 800;
  letter-spacing: -0.5px;
}

.features-subtitle {
  font-size: 1.2rem;
  color: var(--gray-color);
  max-width: 600px;
  margin: 0 auto;
}

.features-grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  justify-content: center;
}

.feature-card {
  position: relative;
  height: 350px;
  background-size: cover;
  background-position: center;
  border-radius: 20px;
  cursor: pointer;
  overflow: hidden;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.1);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.feature-card:hover {
  transform: translateY(-10px) scale(1.02);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.2);
}

.feature-overlay {
  position: absolute;
  inset: 0;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 1.5rem;
  text-align: left;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.7) 30%, transparent 100%);
}

.feature-number {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  opacity: 0.7;
}

.feature-overlay h2 {
  margin: 0 0 0.8rem 0;
  font-size: 1.4rem;
  font-weight: 700;
  line-height: 1.2;
  text-shadow: 0 2px 4px rgba(0,0,0,0.3);
}

.feature-overlay p {
  margin: 0 0 1.5rem 0;
  font-size: 1rem;
  opacity: 0.9;
  line-height: 1.5;
  text-shadow: 0 1px 2px rgba(0,0,0,0.3);
}

.feature-cta {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  font-weight: 600;
  font-size: 1rem;
  padding: 0.5rem 0;
  color: white;
  opacity: 0;
  transform: translateY(10px);
  transition: all 0.3s ease;
  text-shadow: 0 1px 2px rgba(0,0,0,0.3);
}

.feature-card:hover .feature-cta {
  opacity: 1;
  transform: translateY(0);
}

.feature-cta svg {
  transition: transform 0.3s ease;
}

.feature-card:hover .feature-cta svg {
  transform: translateX(5px);
}

@media (max-width: 768px) {
  .menu-toggle {
    display: block;
  }

  .nav-links {
    display: none;
    flex-direction: column;
    width: 100%;
    background-color: var(--secondary-color);
    position: absolute;
    top: 100%;
    left: 0;
    padding: 1rem 0;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }

  .nav-links.active {
    display: flex;
  }

  .nav-links a {
    padding: 0.8rem 2rem;
  }

  .features-title {
    font-size: 2.2rem;
  }
  
  .features-subtitle {
    font-size: 1rem;
  }
  
  .feature-card {
    height: 300px;
  }
  
  .feature-overlay {
    padding: 1rem;
  }
  
  .feature-overlay h2 {
    font-size: 1.3rem;
  }
}